import React, { Component } from "react";
import "./App.css";
import SpaceHeader from "./components/spaceHeader";
import ShipList from "./components/shipList";
import SingleShip from "./components/singleShip";

const apiData = "https://api.spacexdata.com/v3/ships/";

class App extends Component {
  state = {
    ships: null,
    showShips: true,
    singleShip: null
  };
  // dopracowac template func do fetchÃ³w.
  handleShipDetailsDisplay = ship_id => {
    fetch(`${apiData}${ship_id}`)
      .then(results => results.json())
      .then(data =>
        this.setState({
          singleShip: data
        })
      );
  };

  componentDidMount() {
    fetch(`${apiData}`)
      .then(results => results.json())
      .then(data =>
        this.setState({
          ships: data
        })
      );
  }

  render() {
    // /destructuring doczytaj
    const { ships, singleShip } = this.state;
    // const { ship_id } = singleShip;
    return (
      <div className="App">
        <SpaceHeader />
        <div className="shipInfo">
          {ships ? (
            <ShipList
              currentShipId={singleShip ? singleShip.ship_id : null}
              shipList={ships}
              onShipClick={this.handleShipDetailsDisplay}
            />
          ) : (
            ""
          )}
          {singleShip ? <SingleShip singleShip={singleShip} /> : ""}
        </div>
      </div>
    );
  }
}

export default App;
